import WaterfallFlow from '../../../components/common/nairenk-waterfall-flow/nairenk-waterfall-flow.vue';
export default {
	 components: {
	            WaterfallFlow
	        },
	data() {
		return {
			page: 1,
			swiperArr:[{
				image:'/static/avatar.jpg'
			},{
				image:'/static/avatar2.jpg'
			}],
			dotStyle:false,
			cardCur:0,
			list:[],
			list2:[]
		};
	},
	onLoad() {
		this.selectAd();
		this.selectSelfProducts();
		
		setTimeout(()=>{
			this.selectCurrPopularProducts();
		},1000)
	},
	methods: {
		choose(){
			
		},
		choose2(){
			
		},
		cardSwiper(){
			
		},
		//广告位
		async selectAd() {
			
		},
		//本店
		async selectSelfProducts(){
			const res = await this.$util.request({
				requestUrl: 'api/products',
				data: {
					limit: 10,
					page: 1,
					name: null,
					pType: 1,
					online: 1
				}
			});
			console.log('本店：', res);
			if (res !== undefined) {
				this.list2=res.data.data;
			}
		},
		//流行
		async selectCurrPopularProducts() {
			const res = await this.$util.request({
				requestUrl: 'api/products',
				data: {
					limit: 10,
					page: 1,
					name: null,
					pType: 0,
					online: 1
				}
			});
			console.log('流行：', res);
			if (res !== undefined) {
				this.list=res.data.data;
			}
		}
	}
};
